.advantages-section{
    padding-bottom: 97px;
    padding-top: 39px;
    background-color:$color-bg-footer;
}

@media screen and (min-width: 768px){
    .advantages-section{
        padding-bottom: 140px;
    display: flex;
   gap: 60px;
   padding-top: 66px;
  }
}

@media screen and (min-width: 1280px){
    .advantages-section{
        padding-bottom: 139px;
        padding-top: 39px;
    gap:30px
  }
}

@media screen and (min-width: 768px){
    .advantages-section__list{
        display: flex;
  }
}


@media screen and (min-width: 768px){
    .advantages-section__sublist:not(:last-child){
        margin-right: 60px;
    }
}
@media screen and (min-width: 1280px){
    .advantages-section__sublist:not(:last-child){
        margin-right: 30px;
    }
}



.name{
font-family: Titan One;
font-size: 36px;
line-height: 1.14;
letter-spacing: 0.04em;
text-transform: uppercase;
margin-bottom: 16px;
color: $color-primary;
}

@media screen and (min-width: 768px){
    .name{
        font-size: 48px;
        line-height: 55px;
        margin-bottom: 13px;
  }
}

@media screen and (min-width: 1280px){
    .name{
    font-size: 58px;
    line-height: 66px;
    margin-bottom: 15px;
  }
}

.advantages-section__name_a::before{
    content: "";
    display: block;
    width: 100%;
    height: 50px;
    margin-bottom: 16px;
    background-repeat: no-repeat;
   background-size: contain;
    background-image: url("../images/advantages-img-1.png");
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image:
      url("../images/advantages-img-1@2x.png");
  }
  @media screen and (min-width: 768px) {
    margin-top: 0;
    margin-bottom: 36px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 0;
    margin-bottom: 40px;
  }
}


.advantages-section__name_b::before{
    content: "";
    background-image: url("../images/advantages-img-2.png");
    display: block;
    width: 100%;
    height: 50px;
    margin-top: 38px;
    margin-bottom: 16px;
    background-repeat: no-repeat;
    background-size: contain;
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image:
      url("../images/advantages-img-2@2x.png");
  }
  @media screen and (min-width: 768px) {
    margin-top: 0;
    margin-bottom: 36px;
  }
  @media screen and (min-width: 1280px) {
    margin-top: 0;
    margin-bottom: 40px;
  }
}

.advantages-section__name_c::before{
    content: "";
    display: block;
    width: 100%;
    height: 50px;
    margin-bottom: 16px;
    margin-top: 38px;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../images/advantages-img-3.png");
@media screen and (min-width: 768px) {
     margin-top: 0; 
     margin-bottom: 36px;
    }
@media screen and (min-width: 1280px) {
        margin-top: 0;
        margin-bottom: 40px;
    }
}




.advantages-section__text{
font-weight: bold;
font-size: 12px;
line-height: 1.7;
color: $color-black;
}

@media screen and (min-width: 768px){
    .advantages-section__text{
        font-size: 14px;
    line-height: 1.74;
  }
}

@media screen and (min-width: 1280px){
    .advantages-section__text{
        font-size: 16px;
        line-height: 1.76;
       
  }
}