.slider-pfoto {
    margin-bottom: 20px;
    width: 85px;
    height: 85px;
    margin-left: auto;
    margin-right: auto;
    @media screen and (min-width: 768px) {
        margin-top: 120px;
    }
    @media screen and (max-width: 768px) {
        margin-top: 80px;
    }
}

.carousel-caption {
    position: revert;
    right: 15%;
    bottom: 1.25rem;
    left: 15%;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    color: black;
    text-align: center;
}

.color-dot {
    color: #C7133E;
    font-weight: 900;
    font-size: 30px;
    border-radius: 50%;
    margin-bottom: 30px;
    @media screen and (min-width: 1280px) {
        font-size: 30px;
        line-height: 34px;
    }
}

.slider-text {
    background-color: #FAFAFA;
}

.slider-text-decoration {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    letter-spacing: 0.04em;
    background-color: #FAFAFA;
    margin: 0 10%;
    @media screen and (max-width: 768px) {
        font-size: 12px;
        line-height: 255%;
    }
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 216%;
    }
    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 186%;
    }
}

.color-slider_decoration {
    text-align: center;
}

.underline {
    display: inline-block;
    position: relative;
    padding: 0 10px;
    background-image: linear-gradient(transparent, transparent 29px, #E1E1E1 0);
    background-size: 100% 30px;
    z-index: 1;
    margin-top: 30px;
    margin-bottom: 30px;
    @media screen and (max-width: 400px) {
        margin-left: 50px;
        margin-right: 50px;
    }
}

.slider-name-decoration {
    font-family: DM Sans;
    font-style: normal;
    font-weight: 500;
    text-align: center;
    margin-bottom: 75px;
    margin-top: 20px;
    @media screen and (min-width: 1280px) {
        font-size: 20px;
        line-height: 186%;
    }
    @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 186%;
    }
    @media screen and (max-width: 768px) {
        font-size: 12px;
        line-height: 255%;
    }
}

.carousel-control-next-icon,
.carousel-control-prev-icon {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 100% 100%;
    ;
}

.carousel-indicators [data-bs-target] {
    transition: opacity .6s ease;
    background-color: #D31342;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-right: 9px;
    margin-left: 9px;
    margin-top: 35px;
}

.carousel-indicators {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
    padding: 0;
    margin-right: 15%;
    margin-bottom: 1rem;
    margin-left: 15%;
    margin-top: auto;
}

.carousel-inner {
    margin-bottom: 35px;
}

